* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    /* font-size: 36px; */
}

body {
    min-height: 100vh;
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.square {
    width: 400px;
    height: 400px;
    border: 2px solid #000;
    box-shadow: 2px 2px 5px #000;
    border-radius: 0.25rem;
    display: grid;
    place-content: center;
}

.square p {
    text-align: center;
    font-size: 3rem;
}

form {
    width: 400px;
}

label {
    position: absolute;
    left: -99999px;
}

input[type="text"] {
    margin-top: 0.5rem;
    padding: 0.25rem;
    width: 100%;
    font-size: 2rem;
    box-shadow: 2px 2px 5px #000;
    border-radius: 0.25rem;
    outline: none;
}

button {
    width: 100%;
    min-height: 48px;
    margin-top: 0.5rem;
    font-size: 2rem;
    border-radius: 0.25rem;
    box-shadow: 2px 2px 5px #000;
    padding: 0.25rem;
}

ul {
    list-style: none;
    font-size: 1rem;
}

li {
    margin: 0.5rem 0;
}

.col--left {
    text-align: right;
    padding-right: 0.25rem;
}

.col--right {
    padding-left: 0.25rem;
}

table {
    border: 3px solid green; /* Adjust the border style and color as needed */
}

th,
td {
    border: 1px solid black; /* Adjust the border style and color as needed */
}
